,aACA,CAAA,QACA,CAAA,uCAEA,YACE,CAAA,sBACA,CAAA,qBACA,CAAA,QACA,CAAA,yCAGF,oBACE,CAAA,0DACA,wBACE,CAAA,wCAIJ,sBACE,CAAA,yCAGF,kBACE,CAAA,wCAGF,eACE,CAAA,uCAGF,kBACE,CAAA,wCAGF,YACE,CAAA,kBACA,CAAA,cACA,CAAA,sBACA,CAAA,kBACA,CAAA,qBACA,CAAA,iBACA,CAAA,qBACA,CAAA,qBACA,CAAA,YACA,CAAA,iBACA,CAAA,kBACA,CAAA,kBACA,CAAA,sEAEA,iBACE,CAAA,uEAGF,gBACE,CAAA,uEAGF,cACE,CAAA,cACA,CAAA,eACA,CAAA,2CAOF,aACE,CAAA,UACA,CAAA,kBACA,CAAA,8DAIJ,cACE,CAAA,uBACA,CAAA,iBACA,CAAA,YACA,CAAA,sBACA,CAAA,kBACA,CAAA,gBACA,CAAA,qFAEA,oBACE,CAAA,gBACA,CAAA,gBACA,CAAA,mFAGF,wBACE,CAAA,0GAGF,wBACE,CAAA,+MAGF,wBAEE,CAAA,2PAEA,wBACI,CAAA,oGAIN,cACE,CAAA,gBACA,CAAA,iBACA,CAAA,OACA,CAAA,QACA,CAAA,kBACA,CAAA,4GAIA,oBACE,CAAA,yHAGF,aACE,CAAA,wDAEA,CAAA,SACA,CAAA,uBACA,CAAA,2BACA,CAAA,0BACA,CAAA,iIAIJ,YACE,CAAA,2HAGF,YACE,CAAA,8HAGF,YACE,CAAA,4JAGF,YACE,CAAA,0HAGF,QAEE,CAAA,OACA,CAAA,mFAGF,qBACE,CAAA,UACA,CAAA,kBACA,CAAA,UACA,CAAA,mBACA,CAAA,0DAIJ,UACE,CAAA,iBACA,CAAA,kBACA,CAAA,eACA,CAAA,kBACA,CAAA,WACA,CAAA,4DAGF,WACE,CAAA,WACA,CAAA,WACA,CAAA,0DAGF,sBACE,CAAA,WACA,CAAA,WACA,CAAA,yDAGF,WACE,CAAA,WACA,CAAA,WACA,CAAA,4DAGF,WACE,CAAA,WACA,CAAA,YACA,CAAA,0DAGF,sBACE,CAAA,YACA,CAAA,YACA,CAAA,yDAGF,WACE,CAAA,YACA,CAAA,YACA,CAAA,yDAGF,WACE,CAAA,YACA,CAAA,gBACA,CAAA,YACA,CAAA,iCAIJ,YACE,CAAA,sBACA,CAAA,gBACA,CAAA,eAMA,CAAA,kDAJA,kBACE,CAAA,mCAKF,aACE,CAAA,aACA,CAAA,mDAIA,aACE,CAAA,cACA,CAAA,UACA,CAAA,yEAGF,eACE,CAAA,UACA,CAAA,eACA,CAAA,eACA,CAAA,aACA,CAAA,cACA,CAAA,gBACA,CAAA,aACA,CAAA,qBACA,CAAA,2EAEA,aACE,CAAA,gGAGF,wBACE,CAAA,6JAGF,UACE,CAAA,8EAGF,cACE,CAAA,8BAMR,WACE,CAAA,wBACA,CAAA,WACA,CAAA,wBACA,CAAA,aACA,CAAA,iBACA,CAAA,eACA,CAAA,WACA,CAAA,sFAGF,iBACE,CAAA,oDAGF,UACE,CAAA,iBACA,CAAA,KACA,CAAA,MACA,CAAA,OACA,CAAA,QACA,CAAA,qCACA,CAAA,mBACA,CAAA,2EAGF,UACE,CAAA,iBACA,CAAA,KACA,CAAA,MACA,CAAA,OACA,CAAA,QACA,CAAA,qCACA,CAAA,mBACA,CAAA,kCAGF,aACE,CAAA,wBAGF,eACE,CAAA,gBACA,CAAA,iBACA,CAAA,iBACA,CAAA,0DAEA,kBACE,CAAA,gEAGF,kBACE,CAAA,6BAIJ,cACE,CAAA,oDAEA,cACE","sourcesContent":["#_canopy {\n  margin-top: 20px;\n  overflow-anchor: none; // Prevent browser from trying to maintain position when page contents change, which might interfere with window.scrollTo\n\n  /* Removing DOM elements from the bottom of the screen can force the page to scroll upward to the existing elements\n  which is not animated. If we keep a padding on the bottom of the last element just beyond the viewport, then\n  removal of elements from the bottom of the visible stack never brings the real bottom of the page into view, and so\n  the scroll upwards can be animated using the window.scrollTo method. */\n\n  padding-bottom: calc(100vh);\n  min-height: calc(110vh); // to avoid scroll bar appearing when page length changes for small content that doesn't need scrolling\n\n  font-family: \"Times New Roman\", Times, serif; // good default and standardizes layout for browser tests\n\n  h1.canopy-header {\n    text-align: center;\n    margin: auto;\n    margin-top: 20px;\n  }\n\n  hr.canopy-hr {\n    width: 120px;\n    display: block;\n    height: 1px;\n    border: 0;\n    border-top: 5px solid #ccc;\n    margin: 0px auto;\n  }\n\n  h1 + section.canopy-topic-section {\n    margin-top: 30px;\n  }\n\n  section.canopy-topic-section {\n    margin-top: 32px; // has to match .canopy-topic-section hr + p\n  }\n\n  section.canopy-section {\n    margin-top: 32px;\n    unicode-bidi: unset;\n  }\n\n  .canopy-topic-section hr + p {\n    margin-top: 27px;\n  }\n\n  p.canopy-paragraph {\n    overflow: visible; // In case external link icon is at end of line\n    font-size: 21px;\n    max-width: 750px;\n    margin: auto;\n\n    line-height: 1.3;\n    letter-spacing: -.003em;\n    font-weight: 400;\n\n    position: relative; // to allow absolute children\n    unicode-bidi: plaintext; // to allow lines in different directions\n  }\n\n  .canopy-topic-section hr + p {\n    margin-top: 31px; // 35px minus 1px bar thickness\n  }\n\n  code.canopy-code-block {\n    font-family: 'Courier New', Courier, monospace; /* Monospaced font for code */\n    background-color: #eee; /* Light background to highlight code */\n    padding: 2px 4px; /* Small padding around the code */\n    border-radius: 3px; /* Optional: rounded corners */\n    border: .5px solid #ddd; /* Optional: light border */\n    color: #5a5a5a; /* Subtle dark gray color for the code text, less harsh than black */\n  }\n\n  // Tool tip\n\n  .canopy-tooltip {\n    position: relative;\n    display: inline-block;\n    cursor: pointer;\n\n    .canopy-tooltip-icon {\n      font-size: 11px;\n    }\n\n    .canopy-tooltiptext {\n      visibility: hidden;\n      width: 140px;\n      background-color: white;\n      color: black;\n      text-align: center;\n      border-radius: 6px;\n      border: 1px solid black;\n      padding: 10px;\n      position: absolute;\n      z-index: 1;\n      top: 50%;\n      left: 105%;\n      margin-left: 10px;\n      transform: translateY(-50%);\n      opacity: 0;\n      transition: opacity 0.3s;\n      font-size: 14px;\n    }\n\n    &:hover .canopy-tooltiptext {\n      visibility: visible;\n      opacity: 1;\n    }\n  }\n\n  // Text\n  .canopy-text-span {\n    display: inline;\n  }\n\n  .canopy-linebreak-span {\n    display: block;\n    margin-bottom:12px;\n  }\n\n///////////// Link styles /////////////////\n\n  a.canopy-selectable-link {\n    text-decoration: none;\n    \n    background-color: #FFFFFF;        /* Subtle off-white background */\n    padding-left: 1px;\n    padding-right: 1px;\n    border-radius: 1px;\n    border: 1px solid #dddddd;\n\n    &.canopy-linked-image { // ie the caption text etc\n      text-decoration: none; //override browser default for a tags\n    }\n\n    color: black;\n    cursor: pointer;\n\n    &:focus {\n      outline:0;\n    }\n\n    &.canopy-open-link:not(.canopy-selected-link):not(.canopy-table-link):not(.canopy-menu-link-cell) {\n      background-color: #edecec\n    }\n\n    &:hover {\n      background-color: #f5f6fe;\n    }\n\n    &.canopy-selected-link, &.canopy-selected-link code {\n      background-color: #edeefc;\n      //text-shadow: 0px 0px 0px #000000;\n      text-decoration: none;\n    }\n\n    &.canopy-external-link, &.canopy-back-cycle-link, &.canopy-lateral-cycle-link/*, &.canopy-path-reference*/ {\n      &:not(.canopy-menu-link-cell) {\n        display: inline;\n        white-space: normal;\n        margin-right: 0px; // combined with negative margin on the ::after block to prevent following punctuation from wrapping\n        padding-right: 10px;\n      }\n\n      &:not(.canopy-menu-link-cell)::after {\n        font-size: 9px;\n        white-space: nowrap;\n        position: relative;\n        margin-right: -8px;\n      }\n    }\n\n    &.canopy-external-link:not(.canopy-menu-link-cell):not(.canopy-linked-image)::after {\n      content: '\\00a0\\00a0\\00a0';\n      font-size: 10px;\n      /* Open Iconic Icon Set, MIT License, https://iconduck.com/icons/53818/external-link */\n      background-image: url('data:image/svg+xml;base64,PHN2ZyBoZWlnaHQ9IjgiIHZpZXdCb3g9IjAgMCAxMCA4IiB3aWR0aD0iOSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICA8cGF0aCBkPSJNMSAwdjhoOHYtMmgtMXYxaC02di02aDF2LTF6bTQgMCAxLjUgMS41LTIuNSAyLjUgMSAxIDIuNS0yLjUgMS41IDEuNXYtNHoiIHRyYW5zZm9ybT0idHJhbnNsYXRlKDEsIDApIiAvPgo8L3N2Zz4K');\n      background-size: contain;\n      background-repeat: no-repeat;\n      top: 1px;\n      left: 0px;\n      position: relative; /* Adjust positioning if needed */\n      vertical-align: text-bottom; /* Aligns with text */\n    }\n\n    &.canopy-path-reference:not(.canopy-menu-link-cell)::after {\n      //content: '↳';\n      top: 1px;\n      left: 1.5px;\n    }\n\n    &.canopy-back-cycle-link:not(.canopy-menu-link-cell)::after { // if it is open, it has been inlined and isn't a cycle\n      content: '↩';\n      top: 2px;\n      left: 0.5px;\n      text-decoration: none;\n    }\n\n    &.canopy-lateral-cycle-link:not(.canopy-menu-link-cell)::after { // if it is open, it has been inlined and isn't a cycle\n      content: '↪';\n      top: 2px;\n      left: 0px;\n      text-decoration: none;\n    }\n  }\n\n  a.canopy-disabled-link {\n    color: #000000;\n    pointer-events: none;\n    cursor: not-allowed;\n    opacity: 0.7;\n    text-decoration: none;\n    background-color: #e3dfdf;\n  }\n\n/////////////////////////////////////////////\n\n  hr.canopy-footnote-rule {\n    width: 750px;\n    display: block;\n    height: 1px;\n    border: 0;\n    border-top: 1px solid #ccc;\n    margin: 12px 0;\n    padding: 0;\n  }\n\n  sup {\n    font-size: 60%;\n    padding: 2px;\n  }\n\n  .canopy-footnote-span {\n    font-size: 80%;\n    display: block;\n    span { // text after number\n      margin-left: 2px;\n    }\n    &:not(:last-child) {\n      padding-bottom: 7px;\n    }\n  }\n\n  blockquote {\n    padding: 0 .7em;\n    color: #565e67;\n    white-space: pre-wrap;\n\n    a, a.canopy-selectable-link {\n      color: #565e67;\n    }\n\n    &[dir=\"rtl\"] {\n      border-right: 0.25em solid lightgray;\n/*      margin-right: 10px;*/\n    }\n\n    &[dir=\"ltr\"] {\n      border-left: 0.25em solid lightgray;\n/*      margin-left: 10px;*/\n    }\n\n    margin-left: 0px;\n    margin-right: 0px;\n    margin-top: 12px;\n    margin-bottom: 12px;\n\n    a.canopy-selectable-link {\n      text-decoration: underline #D3D3D3;\n\n      //reset button styling\n      background-color: #FFFFFF00;\n      padding-left: 0px;\n      padding-right: 0px;\n      border: none;\n\n      &:hover {\n        text-decoration: underline #000000;\n      }\n    }\n\n    a.canopy-selectable-link.canopy-open-link {\n      background-color: #c9c9c9;\n    }\n\n    .canopy-linebreak-span {\n      margin-bottom: 0px; // unset default\n    }\n\n    .canopy-blockquote-padded-linebreak { // we replace break tags to be able to give them padding\n      margin-bottom: 8px;\n      display: block;\n    }\n  }\n\n  pre {\n    margin: 10px 10px;\n    background-color: #eff0f1;\n    padding: 5px;\n    font-size: 85%;\n  }\n\n  code {\n    background-color: #eff0f1;\n    font-size: 90%;\n  }\n\n  table {\n    margin: 21px auto auto auto;\n    table-layout: auto;\n    max-width: 90%;\n    border-collapse: collapse;\n    border: none; // we put the border on the TDs to allow omitted table cells\n    font-size: 18px;\n    min-width: 500px;\n    table-layout: fixed;\n\n    &:not(:last-child) {\n      margin-bottom: 25px;\n    }\n\n    td, th {\n      border: 1px solid black;\n      text-align: center;\n      padding: 10px;\n\n      &.hidden {\n        display: none;\n      }\n\n      br {\n        display: block; // makes it have a width\n        content: \"\"; // clears default height\n        padding-bottom: 2px;\n      }\n    }\n\n    td.canopy-table-link-cell {\n      cursor: pointer;\n      user-select: text;\n      background-color: #ffffff;\n\n      a.canopy-selectable-link {\n        text-decoration: none;\n        text-shadow: none;\n        background-color: #FFFFFF00;\n        border: none;\n      }\n      \n      &:has(.canopy-selected-link) {\n        background-color: #edeefc;\n      }\n\n      &:has(.canopy-open-link):not(:has(.canopy-selected-link)) {\n        background-color: #F3F3F3;\n      }\n\n      &:hover:not(:has(.canopy-selected-link)), // selected link style overrides hover\n      &:hover:has(.canopy-open-link):not(:has(.canopy-selected-link)) {\n        background-color: #f5f6fe;\n\n        .canopy-selected-link {\n            background-color: #f5f6fe;\n        }\n      }\n\n      &:has(a.canopy-disabled-link) {\n        background-color: #ffffff;\n        color: #000000;\n        cursor: not-allowed;\n        opacity: 0.7;\n        pointer-events: none;\n\n        a.canopy-disabled-link {\n          background-color: #FFFFFF00;\n          text-decoration: none;\n          text-shadow: none;\n        }\n      }\n    }\n  }\n\n  .canopy-menu {\n    display: flex;\n    flex-direction: column;\n    align-items: center; /* This centers the rows within the container */\n    margin: 0 auto;\n    gap: 10px;\n\n    .canopy-menu-row {\n      display: flex;\n      justify-content: center;\n      box-sizing: border-box;\n      gap: 10px;\n    }\n\n    &.canopy-align-right {\n      align-items: flex-end;\n      .canopy-menu-row {\n        justify-content: flex-end;\n      }\n    }\n\n    &.canopy-align-left {\n      align-items: flex-start;\n    }\n\n    &.canopy-align-mixed {\n      align-items: normal;\n    }\n\n    &:not(:first-child) {\n      margin-top: 25px; // has to be in sync with size between section elements for button-paragraph sequences\n    }\n\n    &:not(:last-child) {\n      margin-bottom: 25px;\n    }\n\n    .canopy-menu-cell {\n      display: flex;\n      flex-direction: row; /* Arrange items horizontally */\n      flex-wrap: wrap; /* Allow items to wrap within the cell */\n      justify-content: center; /* Center content horizontally */\n      align-items: center; /* Center content vertically */\n      border: 1px solid black;\n      border-radius: 0px;\n      box-sizing: border-box;\n      background-color: white;\n      padding: 10px; /* Add some padding inside cells */\n      text-align: center; /* This will center the text inside the flex items */\n      align-self: stretch; /* Stretch the child items to fill the cell's width */\n      white-space: normal; /* Allow the text to wrap */\n\n      &.canopy-menu-cell-left-aligned {\n        margin-right: auto;\n      }\n\n      &.canopy-menu-cell-right-aligned {\n        margin-left: auto;\n      }\n\n      .canopy-menu-content-container {\n        display: inline;\n        max-width: 100%;\n        max-height: 100%;\n      }\n\n      &.canopy-menu-ordinal-cell .canopy-menu-content-container {\n        //width: 80%; // to keep the numbers in a similar location in each cell\n      }\n\n      br {\n        display: block; // makes it have a width\n        content: \"\"; // clears default height\n        padding-bottom: 2px;\n      }\n    }\n\n    .canopy-menu-link-cell.canopy-menu-cell {\n      cursor: pointer;\n      border: 1.5px solid black;\n      border-radius: 5px;\n      display: flex;\n      justify-content: center; /* Center content horizontally */\n      align-items: center; /* Center content vertically */\n      user-select: none;\n\n      &.canopy-selectable-link {\n        text-decoration: none;\n        text-shadow: none;\n        user-select: none;\n      }\n\n      &.canopy-selected-link {\n        background-color: #edeefc;\n      }\n\n      &.canopy-open-link:not(.canopy-selected-link) {\n        background-color: #edecec\n      }\n\n      &:hover:not(.canopy-selected-link), // selected link style overrides hover\n      &:hover.canopy-open-link:not(.canopy-selected-link) {\n        background-color: #f5f6fe;\n\n        .canopy-selected-link {\n            background-color: #f5f6fe;\n        }\n      }\n\n      .canopy-menu-content-container::after {\n        font-size: 11px;\n        margin-left: -5px; // Negative margin to pull the icon closer to the last word\n        position: relative; // Allows for position adjustment\n        top: 5px; // Move the icon up (- value) or down (+ value)\n        left: 5px;\n        white-space: nowrap; // Prevents the icon from wrapping\n      }\n\n      &.canopy-external-link {\n        &.canopy-selectable-link * {\n          text-decoration: none;\n        }\n\n        .canopy-menu-content-container::after {\n          content: '\\00a0\\00a0\\00a0';\n          /* Open Iconic Icon Set, MIT License, https://iconduck.com/icons/53818/external-link */\n          background-image: url('data:image/svg+xml;base64,PHN2ZyBoZWlnaHQ9IjgiIHZpZXdCb3g9IjAgMCAxMCA4IiB3aWR0aD0iOSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICA8cGF0aCBkPSJNMSAwdjhoOHYtMmgtMXYxaC02di02aDF2LTF6bTQgMCAxLjUgMS41LTIuNSAyLjUgMSAxIDIuNS0yLjUgMS41IDEuNXYtNHoiIHRyYW5zZm9ybT0idHJhbnNsYXRlKDEsIDApIiAvPgo8L3N2Zz4K');\n          top: 2.5px;\n          background-size: contain;\n          background-repeat: no-repeat;\n          vertical-align: 